/*========= ナビゲーションのためのCSS ===============*/
#g-nav {
  /*position:fixed;にし、z-indexの数値を大きくして前面へ*/
  position: fixed;
  z-index: 999;
  /*ナビのスタート位置と形状*/
  top: 0;
  right: -120%;
  width: 100%;
  height: 100vh;
  /*ナビの高さ*/
  background-color: #0397de;
  /*動き*/
  transition: all 0.6s;
}

/*アクティブクラスがついたら位置を0に*/
#g-nav.panelactive {
  right: 0;
}

/*ナビゲーションの縦スクロール*/
#g-nav.panelactive #g-nav-list {
  /*ナビの数が増えた場合縦スクロール*/
  position: fixed;
  z-index: 999;
  width: 100%;
  height: 100vh;
  /*表示する高さ*/
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

/*========= ボタンのためのCSS ===============*/
.openbtn {
  position: relative;
  z-index: 9999;
  /*ボタンを最前面に*/
  cursor: pointer;
  width: 3.125rem;
  height: 3.125rem;
  display: none;
}

@media screen and (max-width: 767px) {
  .openbtn {
    display: block;
  }
}

/*×に変化*/
.openbtn span {
  display: inline-block;
  transition: all .4s;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 0.125rem;
  border-radius: 2px;
  background-color: #0397de;
  width: 65%;
}

.openbtn span:nth-of-type(1) {
  top: 0.75rem;
}

.openbtn span:nth-of-type(2) {
  top: 1.4375rem;
}

.openbtn span:nth-of-type(3) {
  top: 2.125rem;
}

.openbtn.active span:nth-of-type(1) {
  top: 18px;
  left: 18px;
  transform: translateY(6px) rotate(-45deg);
  width: 60%;
  background-color: #fff;
}

.openbtn.active span:nth-of-type(2) {
  opacity: 0;
}

.openbtn.active span:nth-of-type(3) {
  top: 30px;
  left: 18px;
  transform: translateY(-6px) rotate(45deg);
  width: 60%;
  background-color: #fff;
}

